import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { ConfigProvider, Epic, Tabbar, TabbarItem, View } from '@vkontakte/vkui';
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Home from './panels/Home'
import Home1 from './panels/Home1'
import Home2 from './panels/Home2'

const App = () => {
  const [schema,changeSchema]=React.useState('bright_light')
  const [activeStory,changeActiveStory]=React.useState('feed')
  const [notifi,changeNotifi]=React.useState([{id:0,head:'Вам подарок',text:'Откройте уведомление, что бы получить бонус!'}])
  const [tabbar,changeTabbar]=React.useState(true)
  const [user,changeUser]=React.useState(null)
  
  const onStoryChange = (e) => {
    const a = e.currentTarget.dataset.story;
    changeActiveStory(a?a:'feed')
  }
  const go = (e) => {
    const a = e.currentTarget.dataset.to;
    changeActiveStory(prev=>a!==undefined?a:prev)
  }
  React.useEffect(() => {
    bridge.subscribe((e) => {
      const { type, data } = e.detail;
      if (type === 'VKWebAppUpdateConfig') {
        changeSchema(data.scheme)
      }
      if (type === 'VKWebAppInitResult') {
        changeTabbar(true)
        changeActiveStory('feed')
      }
    });
    bridge.sendPromise('VKWebAppGetUserInfo').then((e)=>{
      changeUser(e)
    })
  }, []);
  return (
    <ConfigProvider isWebView={true} scheme={schema}>
      <Epic activeStory={activeStory} tabbar={tabbar&&
        <Tabbar style={{visibility: 'hidden'}}>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'feed'}
            data-story="feed"
            text="Маски"
          ><Icon28NewsfeedOutline /></TabbarItem>

          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === 'more'}
            data-story="more"
            text="Ещё"
          ><Icon28More /></TabbarItem>
        </Tabbar>
      }>
        <View id="feed" activePanel="home">
          <Home id="home" fetchedUser={user} go={go} />
        </View>
        <View id="notifications" activePanel="home">
          <Home1 id="home" fetchedUser={user} go={go} />
        </View>
        <View id="more" activePanel="home">
          <Home2 id="home" fetchedUser={user} go={go} />
        </View>

      </Epic>
    </ConfigProvider>
  );
}
export default App;
 